export const FinalizeProposalPage = (props: IFinalizeProposalPage.IProps) => {
	console.log('props: ',props);
	const classes = viewClientStyles(); 

	let isApprover = false;


	if(props.match.params.user_type === 'uploader') isApprover = false;
	if(props.match.params.user_type === 'approver') isApprover = true;

	const [isPopoverOpen, setPopupoverOpen] = useState(false);
	const anchorRef = useRef(null);
	const [arrowRef, setArrowRef] = useState<any>(null);
	const [selectedFile, setSelectedFile] = useState("");
	const [selectedFormat, setSelectedFormat] = useState("");
	const [redirectToDashboard, setRedirectToDashboard] = useState(false);
	const [uploaded, setUploaded] = useState<any[]>([]);
	const [imageViewerOpen, setImageViewerOpen] = useState(false);
	const [index, setIndex] = useState(0);
	const [hasSignedProposal] = useState(true);
	const steps = ['Review Details', 'Review Benefits', 'Finalize'];

	const sampleFile = [{
		src: `data:image/jpeg;base64,`,
		name: "674605.jpg"
	}]

 	console.log('uploaded',JSON.stringify(uploaded));

	function handleClose(){
		console.log('setPopupoverOpen');
        setPopupoverOpen(false);
    }

    function handleOpenPopper(){
        setPopupoverOpen(true);
    }

    function handleDownloadFile(){

    }

    function handleDeleteFile(index:number){
    	let temp = Object.assign([],uploaded);
    	// delete temp[index];
    	temp.splice(index,1);
    	setUploaded(temp);
    }

    function handleOpenImageViewer(index:number){
    	setIndex(index);
    	setImageViewerOpen(true);
    }

    function handleCloseImageViewer(){
    	// setIndex(0);
    	setImageViewerOpen(false);
    }

    function handleFileUpload(acceptedFile: any){
    	console.log('acceptedFile',JSON.stringify(acceptedFile[0]));
    	let reader = new FileReader();
		let src: any = null;
		let typeArray;

		reader.onload = function(event) {
			acceptedFile.name = acceptedFile[0].name;
		    acceptedFile.sample = false;
		    acceptedFile.type = acceptedFile.name.toLowerCase().includes('.pdf') ? 'pdf' : 'img';
		    if(event !== null && event.target !== null){
		    	src = (acceptedFile.type !== 'pdf') ? event.target.result : null;
		    	if(acceptedFile.type === 'pdf'){
		    		// typeArray = new Uint8Array(this.result);
		    		src = event.target.result;
		    		console.log('typeArray', typeArray);
		    	}
		    	
		    } 
		    // console.log(event.target !== null,event.target.result)
		    console.log('src: ',src);
		    acceptedFile.src = src;
		    
		    console.log('uploaded',uploaded);
		    setUploaded([...uploaded, acceptedFile]);
		};

		if(acceptedFile[0].name.toLowerCase().includes('.pdf')){
			reader.readAsArrayBuffer(acceptedFile[0]);
		} else {
			reader.readAsDataURL(acceptedFile[0]);
		}
    }

    function submitForSetup(){
		// let startCaseData = {
		// 	pro_uid: '7483319725dae85ea897f26051447119',
		// 	tas_uid: '2494937605dae8613759d85048305887'
		// }
		// if(isApprover){
			Processmaker.put('cases/'+props.match.params.case_id+'/route-case','',{})
				.then(response => {
					console.log('routecase response: ',response);
					setRedirectToDashboard(true);
				});
		// } else {
		// 	Processmaker.postWithToken('cases','',startCaseData)
		// 		.then(response => {
		// 			console.log('startCase response: ',response);

		// 			if(response !== undefined && response.app_uid !== undefined){
		// 				Processmaker.put('cases/'+response.app_uid+'/route-case','',{})
		// 					.then(response => {
		// 						console.log('routecase response: ',response);
		// 					});
		// 			}
		// 		});
		// }

    }

    if(redirectToDashboard){
        return <Redirect to={{pathname: '/underwriting/'}}/>
    } else {
    	return (
			<div style={{padding: '32px 24px'}}>
				{/* <AuthenticatedLayout> */}
					<Grid
	                    container
	                    >
	                    <Grid item xs={12}>
	                        <Grid item xs={12}>
	                            {/*<Components.UI.BreadcrumbsComponent items={breadcrumbs}/>*/}
	                        </Grid>

	                        <Grid item xs={12}>
	                            <PageHeaderComponent
	                                id={"finalize_proposal_stepper"}
	                                label="Finalize Proposal"
	                                steps={steps}
	                                activeStep={2}
	                            />
	                        </Grid>
	                    </Grid>
	                </Grid>
					<Grid container>
						<Grid item xs={12}>
							<Grid container justify="flex-start" alignItems="flex-start">
								<Grid item xs={8}>
									<Typography className={classes.title} color="textPrimary" variant="h5">
										Finalize Proposal
									</Typography>
									<Typography className={classes.subTitle} color="textPrimary">
										Ensure that the attached file/s matches the proposal version
									</Typography>
								</Grid>
								<Grid item xs={4} className={classes.downloadReport}>
									{
										isApprover ? (
					                        <span 
					                            ref={anchorRef}
					                            className={classes.iconsSpan}
					                            onClick={handleOpenPopper}>
					                            <FontAwesomeIcon 
					                                icon={faDownload}
					                                className={classes.icons}
					                            /> Download Proposal
					                            <Popper
					                                open={isPopoverOpen}
					                                anchorEl={anchorRef.current}
					                                placement={"bottom-end"}
					                                className={classes.popper}
					                                disablePortal={false}
					                                style={{marginTop:'4px'}}
					                                modifiers={{
					                                    flip: {
					                                      enabled: false,
					                                    },
					                                    preventOverflow: {
					                                      enabled: true,
					                                      boundariesElement: 'undefined',
					                                    },
					                                    arrow: {
					                                      enabled: true,
					                                      element: arrowRef,
					                                    },
					                                }}
					                            >
					                                <span className={classes.arrow} ref={setArrowRef} />
					                                <Paper className={classes.paper}>
					                                    <ClickAwayListener onClickAway={handleClose}>
					                                        <Grid container>
					                                            <Grid item xs={12}>
					                                                <Typography className={classes.textLabel} color="textPrimary">
					                                                    Choose File
					                                                </Typography>
					                                            </Grid>
					                                            <Grid item xs={12}>
					                                                <RadioGroup
					                                                    aria-label="choose-data"
					                                                    name="choose-data"
					                                                    value={selectedFile}
					                                                    style={{paddingLeft:'5px'}}
					                                                    onChange={e => setSelectedFile(e.target.value)}>
					                                                    {
					                                                    	hasSignedProposal ? (
					                                                    		<FormControlLabel
							                                                    	className={classes.text}
							                                                        value="Signed Proposal only"
							                                                        control={<Radio style={{padding:'5px'}} color="primary" />}
							                                                        label={<Typography className={classes.text}>Signed Proposal only</Typography>}
							                                                        labelPlacement="end"
							                                                    />
					                                                    	): null
					                                                    }
					                                                    
					                                                    <FormControlLabel
					                                                    	className={classes.text}
					                                                        value="This version of the proposal"
					                                                        control={<Radio style={{padding:'5px'}} color="primary" />}
					                                                        label={<Typography className={classes.text}>This version of the proposal</Typography>}
					                                                        labelPlacement="end"
					                                                    />
					                                                    <FormControlLabel
					                                                    	className={classes.text}
					                                                        value="Both"
					                                                        control={<Radio style={{padding:'5px'}} color="primary" />}
					                                                        label={<Typography className={classes.text}>Both</Typography>}
					                                                        labelPlacement="end"
					                                                    />
					                                                </RadioGroup>
					                                            </Grid>
					                                            <Grid item xs={12}>
					                                                <Typography className={classes.textLabel} color="textPrimary">
					                                                    Choose Format
					                                                </Typography>
					                                            </Grid>
					                                            <Grid item xs={12}>
					                                                <RadioGroup
					                                                    aria-label="choose-format"
					                                                    name="choose-format"
					                                                    value={selectedFormat}
					                                                    style={{paddingLeft:'5px'}}
					                                                    onChange={e => setSelectedFormat(e.target.value)}>
					                                                    <FormControlLabel
					                                                    	className={classes.text}
					                                                        value="PDF"
					                                                        control={<Radio style={{padding:'5px'}} color="primary" />}
					                                                        label={<Typography className={classes.text}>PDF</Typography>}
					                                                        labelPlacement="end"
					                                                    />
					                                                    <FormControlLabel
					                                                    	className={classes.text}
					                                                        value="DOC"
					                                                        control={<Radio style={{padding:'5px'}} color="primary" />}
					                                                        label={<Typography className={classes.text}>DOC</Typography>}
					                                                        labelPlacement="end"
					                                                    />
					                                                </RadioGroup>
					                                            </Grid>
					                                            <Grid item xs={12} style={{textAlign: 'center', marginTop: '10px'}}>
					                                                <Button className={classes.downloadBtn} variant="contained" color="secondary" onClick={handleDownloadFile}>
					                                                    Download File
					                                                </Button>
					                                            </Grid>
					                                        </Grid>
					                                    </ClickAwayListener>
					                                </Paper>
					                            </Popper>
					                        </span>
										) : null
									}
								</Grid>
							</Grid>
						</Grid>
						<Grid item xs={12}>
							<Grid container direction="row" justify="flex-start" alignItems="flex-start">
								<Grid item xs={12}>
									<Grid container direction="row" justify="flex-start" alignItems="flex-start" className={classes.uploadFilesContainer}>
										{
											!isApprover ? (
												uploaded.map((item:any, index:number) => {
													return (
														<Grid item className={classes.uploadFiles} >
															{
																(item.type !== 'pdf') ? (
																	<img src={item.src} className={classes.uploaded} 
																		onClick={(e)=> {
																			handleOpenImageViewer(index)
																			console.log('clicked ',e);
																		}}/>
																) : (
																	<div className={classes.thumbnail}
																		onClick={(e)=> {
																			handleOpenImageViewer(index)
																			console.log('clicked ',e);
																		}}
																	>
																		<FontAwesomeIcon className="fa-4x" icon={faFilePdf} style={{ color: 'rgba(39, 46, 76, 0.56)', position: 'absolute', top: '35%', bottom: '50%', left: '40%', right: '50%' }} />
																	</div>
																)
															}
															<Typography color="textPrimary" style={{paddingTop:'5px', textAlign:'center', width: 182}}>
							                                    <span>
							                                        <span style={{textDecoration: 'none', fontSize:'10px'}}>{item.name}</span>
							                                        {
							                                        	!isApprover ? (
							                                        		<span 
									                                            style={{ paddingLeft: '10px', fontSize:'15px'}} 
									                                            onClick={e => {
									                                                handleDeleteFile(index);
									                                                console.log(e);
									                                            }}
									                                        >
									                                            <FontAwesomeIcon icon={faTimesCircle} style={{ color: '#B5B8C1' }} />
									                                        </span>
							                                        	) : null
							                                        }
							                                    </span>
							                                </Typography>
														</Grid>
													);
												})
											) : (
												<Grid item className={classes.uploadFiles} >
													<img src={sampleFile[0].src} className={classes.uploaded} 
														onClick={(e)=> {
															handleOpenImageViewer(0)
															console.log('clicked ',e);
														}}/>
													<Typography color="textPrimary" style={{paddingTop:'5px', textAlign:'center'}}>
					                                    <span>
					                                        <span style={{textDecoration: 'none', fontSize:'10px'}}>{sampleFile[0].name}</span>
					                                    </span>
					                                </Typography>
												</Grid>
											)
										}
										<Grid item xs={1} className={classes.dropzone}>
											{
												!isApprover ? (
													<Dropzone
							                            onDrop={acceptedFiles => handleFileUpload(acceptedFiles)}
							                            accept=".jpg, .jpeg, .pdf, .JPG, .PNG, .png">
							                            {({getRootProps, getInputProps}) => (
							                                <section>
							                                    <div className={classes.uploadFile} {...getRootProps()}>
							                                        <input id="upload_file_member" {...getInputProps()} />
							                                        <FontAwesomeIcon className="fa-2x" icon={faFileUpload} style={{ color: 'rgba(39, 46, 76, 0.56)' }}/>
							                                        <p style={{ color: 'rgba(39, 46, 76, 0.56)', fontWeight: 400 }}>
							                                            Drag or drop file
							                                        </p>
							                                    </div>
							                                </section>
							                            )}
							                        </Dropzone>
												) : null
											}
										</Grid>
									</Grid>
								</Grid>
							</Grid>
							
						</Grid>
					</Grid>
					<Grid
	                    container
	                    alignItems="stretch">
	                    <Grid item xs={12}>
	                        <MyFloatingButtons
	                            submitForSetup={submitForSetup}
	                            isApprover={isApprover}
	                        />
	                    </Grid>
	                </Grid>
					{ imageViewerOpen ?
	                    (
	                    	!isApprover ? (
	                    		<ImageViewer
									images={uploaded}
									index={index}
									onClose={handleCloseImageViewer}
								/>
	                    	) : (
	                    		<ImageViewer
									images={sampleFile}
									index={index}
									onClose={handleCloseImageViewer}
								/>
							)
	                    ) : null
	                }
				{/* </AuthenticatedLayout> */}
			</div>
		);	
    }
	
};

const mapStateToProps = (state: Store) => state.home;

const mapDispatchToProps = (dispatch: Dispatch) => ({
	Map: bindActionCreators(FinalizeProposalActions.Map, dispatch),
});

export { mapStateToProps, mapDispatchToProps };
